#openseadragon1 {
  height: 100%;
}

.slider {
  -webkit-appearance: none;
  width: 100%;
  height: 15px;
  border-radius: 5px;  
  background: #d3d3d3;
  outline: none;
  opacity: 0.7;
  -webkit-transition: .2s;
  transition: opacity .2s;
}

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 20px;
  height: 20px;
  border-radius: 50%; 
  background: #a7aba9;
  cursor: pointer;
}

.slider::-moz-range-thumb {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: #a7aba9;
  cursor: pointer;
}

.thumbnails.container {
  background: #fff;
  width: auto;
  display: flex;
  flex-flow: row wrap;
  position: relative;
}

.thumbnails.item {
  background: #fff;
  margin: 4px;
  flex: 0 1 calc(20% - 8px); /* <-- adjusting for margin */
}

#thumbnails {
  position: initial;
  bottom: initial;
  top: initial;
  background: #fff;
}

#thumbnails {
  padding-left: 300px;
  padding-right: 2em;
  padding-top: 2em;
  padding-bottom: 2em;
}

.pagemeta-hidden {
  margin-left: initial;
  padding-right: initial;
}

.pagemeta-hidden #thumbnails {
  padding-left: 3em;
  padding-right: initial;
}

.my-image-list {
  @include image-list.standard-columns(5);
